import React, { useState } from "react";

const Image = ({ inc }) => {
  return (
    <>
      <div className="ImgAll">
        <img
          src="https://www.searchpng.com/wp-content/uploads/2019/02/Shopping-Cart-PNG-Icon.png"
          width="120px"
          height="120px"
        />
        <h3 className="Imgh3">{inc}</h3>
      </div>
    </>
  );
};

const Add = () => {
  const data = [
    {
      phone: "Samsung A1",
      price: 40000
    },
    {
      phone: "Samsung A2",
      price: 35000
    }
  ];

  const [inc, setinc] = useState(0);
  const [details, setdetails] = useState([
    {
      name: "",
      price: ""
    }
  ]);
  return (
    <>
      <div
        className="compo"
        style={{ display: "flex", flexDirection: "column" }}
      >
        <div>
          {data.map((val) => {
            return (
              <>
                <div className="compoHome">
                  <div className="compoP1">
                    <img
                      src="https://clipart.info/images/ccovers/1503608343smartphone%20samsung%20galaxy%20mobile%20png.png"
                      width="100px"
                      height="200px"
                    />
                    <div style={{ textAlign: "center", marginTop: "50px" }}>
                      <h1>{val.phone} </h1>
                      <h3>Rs. {val.price}</h3>
                    </div>
                  </div>
                  <button
                    style={{
                      cursor: "pointer",
                      padding: "20px 20px",
                      fontSize: "20px",
                      color: "White",
                      backgroundColor: "blue"
                    }}
                    onClick={() => {
                      // setinc(inc + 1);
                      alert(
                        `Phone : ${val.phone} Price : ${val.price} added to cart successfullty`
                      );
                      setinc(inc + 1);
                    }}
                  >
                    Add to cart
                  </button>
                </div>
              </>
            );
          })}
        </div>

        <div>
          <Image inc={inc} setinc={setinc} />
        </div>
      </div>
    </>
  );
};

export default Add;
